import React from 'react';
import "./hero.scss";

function HeroTag({tag}) {
    if (tag) {
        return <div className="hero__tag">{tag}</div>;
    }
    return '';
}

function HeroTitle({title}) {
    return <header className="hero__header">
        <h1 className="hero__title">{title}</h1>
    </header>
}

function HeroAuthor({author}) {
    if (author) {
        return <div className="hero__author">
            {author}
        </div>;
    }
    return '';
}

/**
 * @param department
 * @returns {null}
 */
function HeroDepartment({department}) {
    return department ? <div className="hero__department">{department}</div> : null;
}

/**
 * @param tag string
 *   Top line of text
 * @param title string
 *   Main title of text
 * @param string author
 *   Small line of text below the title (used for the content author)
 * @param department string
 *   Text beneath the author, usually for the author's job title.
 */
export default function Hero({tag, title, author, department}) {
    return <div className="hero">
        <HeroTag tag={tag} />
        <HeroTitle title={title} />
        <HeroAuthor author={author} />
        <HeroDepartment department={department} />
    </div>;
}



