import React from 'react';
import './style.scss';
import logoSrc from './logo.svg'
import { withRouter } from 'react-router';

function HeaderMenuItem({href, text, currentPath}) {
    const isCurrentPath = currentPath.startsWith(href);
    const classNames = `header__nav__link js-header-nav-link ${isCurrentPath ? 'header__nav__link--active' : ''}`;
    return <li className="header__nav__item"><a href={href} className={classNames}>{text}</a></li>
}

class Header extends React.Component {

    render() {
        const { router, params, location, routes } = this.props;

        return <header className="header">
                <div className="header__container">
                    <div className="header__logo--mobile">
                        <a href="/">
                            <img src={logoSrc} className="header__logo-img" alt="Deeson" />
                        </a>
                    </div>
                    <nav className="header__nav">
                        <ul className="header__nav__list">
                            <li className="header__nav__logo">
                                <a href="/" className="header__nav__logo-link js-header-nav-link">
                                    <img src={logoSrc} className="header__logo-img" alt="Deeson" width="138" height="25" />
                                </a>
                            </li>
                            <HeaderMenuItem href="/work" text="Work" currentPath={location.pathname} />
                            <HeaderMenuItem href="/about" text="About" currentPath={location.pathname} />
                            <HeaderMenuItem href="/services" text="Services" currentPath={location.pathname} />
                            <HeaderMenuItem href="/careers" text="Careers" currentPath={location.pathname} />
                            <HeaderMenuItem href="/blog" text="Blog" currentPath={location.pathname} />
                            <HeaderMenuItem href="/contact" text="Contact" currentPath={location.pathname} />
                        </ul>
                    </nav>
                    <a href="#" className="header__nav-icon js-toggle-mobile-menu">
                        menu
                        <span></span>
                        <span></span>
                        <span></span>
                        <span></span>
                    </a>
                </div>
            </header>;
    }
}

export default withRouter(Header)



