import React from 'react';
import './parallax.scss';

export default class Parallax extends React.Component {
    componentDidMount() {
        const parallaxItem = document.querySelector('.rellax');

        if (!parallaxItem) return;

        const Rellax = require('rellax');
        const imagesLoaded = require('imagesloaded');

        imagesLoaded('.site-wrapper', () => {
            var rellax = new Rellax('.rellax');
        });
    }

    render() {
      return <div className="parallax-contain">
        <div className="parallax parallax-1">
            <div className="parallax__grey-triangle rellax" data-rellax-speed="-1.1"></div>
            <div className="parallax__blue-circle rellax" data-rellax-speed="-4.5"></div>
            <div className="parallax__grey-circle rellax" data-rellax-speed="-1.5"></div>
            <div className="parallax__green-circle rellax" data-rellax-speed="1.2"></div>
            <div className="parallax__red-circle rellax" data-rellax-speed="-2.2"></div>
        </div>
      </div>
      }
}



