/**
 *
 * @param props.center boolean
 *   Set to true to add a center alignment class.
 * @param props.text HTML
 *   A rich HTML block.
 *
 * @returns {*}
 * @constructor
 */
export default function TextBlock(props) {
    let textClass = 'text-block';

    if (props.center) {
        textClass += ' text-block--center';
    }

    return <div className={ textClass } dangerouslySetInnerHTML={{__html: props.text}} />
}



