import React from 'react';
import './caseStudyCard.scss';
import './screen.png';
import ButtonLink from "../buttonLink";

function CaseStudyCardDescription(props) {
    if (props.description) {
        return <p className="case-study-card__intro">{ props.description }</p>
    }
    return '';
}

// @todo - this should optionally link to the case study.
function CaseStudyCardClientName(props) {
    if (props.clientName) {
        return <h3 className="case-study-card__heading">
                {props.clientName}
        </h3>;
    }
    return '';
}

// @todo - image should be wrapped in an optional link to the case study.
function CaseStudyImage(props) {
    const altText = props.clientName ? props.clientName : '';

    if (props.imgSrc) {
        return <img className="case-study-card__image" src={props.imgSrc} alt={altText} />
    }
    return '';
}

function CaseStudyProjectSummary(props) {
    if (props.summary) {
        return <div className="case-study-card__summary" dangerouslySetInnerHTML={{__html: props.summary}} />
    }
    return '';
}

function CaseStudyButton(props) {
    if (props.link) {
        return <ButtonLink link={props.link} text={props.isExternalLink ? 'Visit the site' : 'View case study'} arrowClass={true}/>
    }
    return '';
}

/**
 * @param props.triangleCornerTop boolean
 *   True puts the white triangle corner in the top left.
 * @param props.backgroundColor string
 *   Color of the background - e.g. 'rgb(248, 248, 246)'
 * @param props.description string
 *   Short by line at the top of the card - e.g. 'Exploring conflict from WW1 to the present day.'
 * @param props.clientName string
 *   The name of the client - e.g. 'Imperial War Museums.'
 * @param props.imgSrc string
 *   URL to an image
 * @param props.summary string
 *   The summary paragraph of text.
 * @param props.link string
 *   A URL to the page the case study is about
 * @param props.isExternalLink boolean
 *   Mark if this is outside the website. We use different button label for external.
 */
export default function CaseStudyCard(props) {
    const triangleClass = props.triangleCornerTop ? 'case-study-card-triangle--top-left' : 'case-study-card-triangle--bottom-left';
    const outerClasses = `case-study-card ${triangleClass} js-case-study-card`;

    return <div className={outerClasses} style={{backgroundColor: props.backgroundColor}}>
        <div className="case-study-card__body">
            <CaseStudyCardDescription description={props.description} />
            <CaseStudyCardClientName clientName={props.clientName} link={props.link} />
            <CaseStudyImage imgSrc={props.imgSrc} clientName={props.clientName} link={props.link}/>
            <CaseStudyProjectSummary summary={props.summary} />
            <CaseStudyButton link={props.link} isExternalLink={props.isExternalLink} />
        </div>
    </div>;
}



