import React from 'react'
import {withRouteData} from "react-static";
import CompleteD from './completeD.svg';

export default withRouteData(({ page }) => {
  return <div>
      <section className="super-hero">
          <div className="super-hero__text">
              <h1 className="super-hero__heading">High profile, high stakes digital transformation</h1>
              <a href="/work" className="btn  btn--arrow btn--center">View our work</a>
          </div>
          <div className="super-hero__icon">
              <img src={CompleteD} alt="Deeson D"/>
          </div>
      </section>
  </div>;
})



