import React from 'react'
import { withRouteData, Link } from 'react-static'

export default withRouteData(({ blogPosts }) => {
    return <div>
        <h1>It's blog time.</h1>
        <ul>
            {blogPosts.map(post => (
                <li key={post.id}>
                    <Link to={post.slug}>{post.title}</Link>
                </li>
            ))}
        </ul>
    </div>;
})



