import React from 'react';
import './caseStudyList.scss'
import CaseStudyCard from '../caseStudyCard';

// @todo - code split?
import imagesLoaded from 'imagesloaded';
import inView from 'in-view';

/**
 * @param this.props.caseStudies Object[]
 *     An array of objects which define a caseStudyCard
 */
export default class CaseStudyList extends React.Component {

    componentDidMount() {

        const grid = document.querySelector('.js-case-study-list');
        const headerCtaBlock = document.querySelector('.header-cta-block');

        // @todo - something else that can be code split?
        const Isotope = require('isotope-layout');

        if (!grid) return;

        inView('.js-case-study-card')
            .on('enter', card => {
                card.classList.add('is-visible');
            });

        imagesLoaded('.js-case-study-list', () => {

            const offsetSecondBlock = () => {
                const cards = Array.from(document.querySelectorAll('.js-case-study-card'));
                const cardOffset = `${cards[0].clientHeight / 2}px`;
                const secondCard = cards[1];
                secondCard.style.marginTop = cardOffset;
            };

            if (!headerCtaBlock) {
                offsetSecondBlock();
            }

            const layout = new Isotope(grid, {
                itemSelector: '.js-case-study-card',
            });
        });
    }

    render() {
        const colors = [
            "rgb(207, 198, 204)",
            "rgb(64, 176, 155)",
            "rgb(248, 248, 246)",
            "rgb(85, 149, 210)",
        ];

        const cards = this.props.caseStudies.map((card, key) => (
            <CaseStudyCard key={key} triangleCornerTop={key % 2 === 1} backgroundColor={colors[key%4]} {...card} />
        ));

        return <section className="case-study-list js-case-study-list">
            {cards}
        </section>;
    }
}



