import React from 'react';
import './buttonLink.scss';

/**
 *
 * @param props.type string
 *   A modifier class to apply to the button.
 * @param props.arrowClass boolean
 *   True to include the arrow.
 * @param props.link string
 *   URL to link to
 * @param props.text
 *   The text of the button.
 */
export default function ButtonLink(props) {
    const classNames = `btn btn--center ${props.type ? 'btn--' + props.type : ''} ${props.arrowClass ? 'btn--arrow' : ''}`;
    return <a href={props.link} className={classNames}>{props.text}</a>;
}



