import React from 'react';
import './style.scss';

import imageLogo from "./logo.svg";
import imageUpperMiddle from "./upper-middle.png";
import imageUpperMiddle2x from "./upper-middle@2x.png";
import imageUpperMiddle3x from "./upper-middle@3x.png";

import "./facebook.svg";
import "./twitter.svg";
import "./medium.svg";
import "./linkedin.svg";

import "../triangle/triangle.svg";
import "../buttonLink/btnArrow.svg";

let srcSet = imageUpperMiddle2x + ' 2x, ' + imageUpperMiddle3x + ' 3x';

export default function Footer(props) {
    return <div className="footer">
        <div className="footer__container">
            <div className="footer__header">
                <div className="footer__social">
                    <a className="footer__social-link footer__twitter" target="_blank" rel="noopener" href="https://twitter.com/DeesonAgency">twitter</a>
                    <a className="footer__social-link footer__facebook" target="_blank" rel="noopener" href="https://www.facebook.com/deesonagency">facebook</a>
                    <a className="footer__social-link footer__medium" target="_blank" rel="noopener" href="https://medium.com/deeson-publication">medium</a>
                    <a className="footer__social-link footer__linkedin" target="_blank" rel="noopener" href="https://www.linkedin.com/company/deeson/">linkedin</a>
                </div>
                <img src={imageUpperMiddle}
                     srcSet={srcSet} className="upper-middle" width="190" height="190" alt="Deeson puzzle icon" />
            </div>
        </div>

        <div className="footer__body">
            <div className="footer__container">
                <div className="footer__info">
                    <div className="footer__info__left">
                        <h4 className="footer__info-heading">Read our thoughts.</h4>
                        <p>Forget telepathy. Our founder writes an email every month to let you know what we're thinking about.</p>
                        <a className="footer__btn" href="/newsletter">Find out more</a>
                    </div>
                    <div className="footer__info__right">
                        <h4 className="footer__info-heading">Events by Deeson.</h4>
                        <p>Thought-provoking discussions on the future of digital. Request an invite to our next briefing or drinks evening.</p>
                        <a className="footer__btn" href="/events">Join us</a>
                    </div>
                </div>
                <div className="footer__sub">
                    <img className="footer__logo" src={imageLogo} width="150" height="31" alt="Deeson logo" />
                    <ul className="footer__address" itemScope itemType="http://schema.org/LocalBusiness">
                        <li itemProp="name">Deeson Group Ltd.</li>
                        <li>
                            <span itemProp="streetAddress">1 Pancras Square</span>
                            <span itemProp="addressLocality">London</span>
                            <span itemProp="postalCode">N1C 4AG.</span>
                        </li>
                        <li>Company number: 01073356.</li>
                        <li>Registered in England &amp; Wales.</li>
                        <li><a href="/privacy-and-cookies">Privacy policy.</a></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>;
}



